<template>
  <div>
    <Width100>
      <Paragraph
        v-bind:center="true"
        title="Information Architecture"
        paragraph="By analyzing Lauren and Danny's tattoo journey, I was then able to narrow down the features via card sorting for the necessary features for Ink Tank to function. I created a site map to organize the pages, information, and nagivation of the app."
      />
    </Width100>

    <Width100>
      <Width50>
        <OrderedList
          title="Learnings"
          titleColor="#0074D9"
          v-bind:items="learnings"
        />
      </Width50>
      <Width50>
        <OrderedList
          title="Improvements"
          titleColor="#0074D9"
          v-bind:items="improvements"
        />
      </Width50>
    </Width100>

    <Width100>
      <Carousel
        v-bind:items="items"
        :containerStyle="containerStyle"
        :imgStyle="imgStyle"
      />
    </Width100>
    <LineBreak />
  </div>
</template>

<script>
import Carousel from '../common/Carousel';
import LineBreak from '../common/LineBreak';
import OrderedList from '../common/OrderedList';
import Paragraph from '../common/Paragraph';
import VueImage from '../common/VueImage';
import Width50 from '../common/layout/Width50';
import Width100 from '../common/layout/Width100';

import inktank_affinitymap1 from "../../assets/img/inktank_affinitymap1.svg";
import inktank_sitemap from "../../assets/img/inktank_sitemap.jpg";
import card_sorting from "../../assets/img/card_sorting.jpg";
import similarity_matrix from "../../assets/img/similarity_matrix.jpg";

export default {
  components: {
    Carousel,
    LineBreak,
    OrderedList,
    Paragraph,
    Width50,
    Width100,
    VueImage,
  },
  data() {
    return {
      inktank_affinitymap1,
      items: [
        {img: inktank_sitemap, alt: "Information Architecture"},
        {img: card_sorting, alt: "Information Architecture"},
        {img: similarity_matrix, alt: "Information Architecture"},
      ],
      containerStyle: {
        height: "100%"
      },
      imgStyle: {
        padding: "24px",
        width: "80%",
        margin: "0 auto",
      },
      learnings: [
        "Some participants grouped check messages under \"Profile\" or \"Notification\"",
        "A participant expected to see \"Terms of Use\" when signing up first.",
        "Some participants expect to view their upcoming appointments or reschedule appointments under \"Profile\" or \"Notification\"",
        "From the Best Merge Result, search, setting, and home were the main categories.",
      ],
      improvements: [
        "The Participant-centric Analysis and Best Merge method were very useful in analyzing my participants’ patterns. With more participants, it could have produced better results.",
        "The top pages have to include search and settings.",
        "Viewing appointment status will be moved to \"Profile\"",
      ],
    };
  }

};
</script>



