<template>
  <div>
    <Landing />
    <ProblemStatement />
    <Discovery />
    <UserInterview />
    <Persona />
    <UserJourney />
    <InformationArchitecture />
    <Prototyping />
    <UsabilityTesting />
    <Iteration />
    <DesignLang />
    <FinalUi />
    <KeyLearnings />
    <NextProjectRouter
      :currentIdx="3"
    />
  </div>
</template>

<script>
import Landing from './Landing';
import ProblemStatement from './ProblemStatement';
import Discovery from './Discovery';
import UserInterview from './UserInterview';
import Persona from './Persona';
import UserJourney from './UserJourney';
import InformationArchitecture from './InformationArchitecture';
import Prototyping from './Prototyping';
import UsabilityTesting from "./UsabilityTesting";
import Iteration from "./Iteration";
import DesignLang from "./DesignLang";
import FinalUi from "./FinalUi";
import KeyLearnings from "./KeyLearnings";
import NextProjectRouter from "../NextProjectRouter";

export default {
  name: 'InkTanks',
  components: {
    Landing,
    ProblemStatement,
    Discovery,
    UserInterview,
    Persona,
    UserJourney,
    InformationArchitecture,
    Prototyping,
    UsabilityTesting,
    Iteration,
    DesignLang,
    FinalUi,
    KeyLearnings,
    NextProjectRouter,
  },
  mounted () {
    this.$ga.page({
      page: '/inktank',
      title: 'inktank',
      location: window.location.href
    });
  },
};
</script>



