<template>
  <div>
    <Width100>
      <Paragraph
        v-bind:center="true"
        title="Discovery"
        subtitle="Competitive Analysis"
        paragraph="I used competitive analysis to learn about the landscape and learn from other apps' strategies. I looked at 2 types of Tattoo apps: Tattoodo and Tattoo Planet."
      />
    </Width100>

    <Width100>
      <OrderedList
        title="Key Findings"
        titleColor="#0074D9"
        v-bind:items="keyFindings"
      />
    </Width100>
    <LineBreak />
  </div>
</template>

<script>
import LineBreak from '../common/LineBreak';
import OrderedList from '../common/OrderedList';
import Paragraph from '../common/Paragraph';
import Width50 from '../common/layout/Width50';
import Width100 from '../common/layout/Width100';

export default {
  components: {
    LineBreak,
    OrderedList,
    Paragraph,
    Width50,
    Width100,
  },
  data() {
    return {
      keyFindings: [
        "Offer features to book consultations with reputable tattoo studios around the world with the click of a button.",
        "With tattoo articles, artist's spotlights, and interviews, users can fully grasps their knowledge on tattoos and find the perfect artist for them.",
        "Competitors have calendar feature where they can make appointment and messaging platform.",
        "Need an advanced search function and scheduling appointment function.",
      ],
    };
  }

};
</script>



