<template>
  <ParagraphWithCarousel
    title="Design Language System"
    :carouselStyle="containerStyle"
    :carouselItems="items"
    :imgStyle="imgStyle"
  />
</template>

<script>
import ParagraphWithCarousel from '../common/ParagraphWithCarousel';

import color from "../../assets/img/color.jpg";
import typography from "../../assets/img/typography.jpg";
import bars from "../../assets/img/bars.jpg";
import button from "../../assets/img/button.jpg";
import alert from "../../assets/img/alert.jpg";
import pickers from "../../assets/img/pickers.jpg";
import snack_bar from "../../assets/img/snack_bar.jpg";
import grid from "../../assets/img/grid.jpg";
import icons from "../../assets/img/icons.jpg";
import imagery from "../../assets/img/imagery.jpg";
import tone from "../../assets/img/tone.jpg";

export default {
  components: {
    ParagraphWithCarousel,
  },
  data() {
    return {
      items: [
        {img: color, alt:"Design Language System"},
        {img: typography, alt:"Design Language System"},
        {img: bars, alt:"Design Language System"},
        {img: button, alt:"Design Language System"},
        {img: alert, alt:"Design Language System"},
        {img: pickers, alt:"Design Language System"},
        {img: snack_bar, alt:"Design Language System"},
        {img: grid, alt:"Design Language System"},
        {img: icons, alt:"Design Language System"},
        {img: imagery, alt:"Design Language System"},
        {img: tone, alt:"Design Language System"},
      ],
      containerStyle: {
        height: "100%"
      },
      imgStyle: {
        padding: "24px",
        width: "80%",
        margin: "0 auto",
      },
    };
  }

};
</script>

<style lang="scss" scoped>

</style>



