<template>
  <ParagraphWithCarousel
    title="Iteration"
    paragraph="Based on the feedback received, I went back to adjust key task flows accordingly."
    :carouselStyle="containerStyle"
    :carouselItems="items"
    :imgStyle="imgStyle"
  />
</template>

<script>
import ParagraphWithCarousel from '../common/ParagraphWithCarousel';

import inktank_iteration1 from "../../assets/img/inktank_iteration1.jpg";
import inktank_iteration2 from "../../assets/img/inktank_iteration2.jpg";
import inktank_iteration3 from "../../assets/img/inktank_iteration3.jpg";
import inktank_iteration4 from "../../assets/img/inktank_iteration4.jpg";
import inktank_iteration5 from "../../assets/img/inktank_iteration5.jpg";

export default {
  components: {
    ParagraphWithCarousel,
  },
  data() {
    return {
      items: [
        {img: inktank_iteration1, alt: "Iteration mocks"},
        {img: inktank_iteration2, alt: "Iteration mocks"},
        {img: inktank_iteration3, alt: "Iteration mocks"},
        {img: inktank_iteration4, alt: "Iteration mocks"},
        {img: inktank_iteration5, alt: "Iteration mocks"},
      ],
      containerStyle: {
        height: "100%"
      },
      imgStyle: {
        padding: "24px",
        width: "80%",
        margin: "0 auto",
      },
    };
  }

};
</script>

<style lang="scss" scoped>

</style>



