<template>
  <ParagraphWithCarousel
    title="Final UI Design"
    paragraph="Integrating Visual Design Principles, iOS Guidelines, accessibility and feedback from fellow designers, I have repolished my designs."
    :carouselStyle="containerStyle"
    :carouselItems="items"
    :imgStyle="imgStyle"
  />
</template>

<script>
import ParagraphWithCarousel from '../common/ParagraphWithCarousel';

import inktank_finalui1 from "../../assets/img/inktank_finalui1.jpg";
import inktank_finalui2 from "../../assets/img/inktank_finalui2.jpg";
import inktank_finalui3 from "../../assets/img/inktank_finalui3.jpg";
import inktank_finalui4 from "../../assets/img/inktank_finalui4.jpg";
import inktank_finalui5 from "../../assets/img/inktank_finalui5.jpg";

export default {
  components: {
    ParagraphWithCarousel,
  },
  data() {
    return {
      items: [
        {img: inktank_finalui1, alt: "Final UI Design"},
        {img: inktank_finalui2, alt: "Final UI Design"},
        {img: inktank_finalui3, alt: "Final UI Design"},
        {img: inktank_finalui4, alt: "Final UI Design"},
        {img: inktank_finalui5, alt: "Final UI Design"},
      ],
      containerStyle: {
        height: "100%"
      },
      imgStyle: {
        padding: "24px",
        width: "80%",
        margin: "0 auto",
      },
    };
  }

};
</script>



