<template>
  <div>
    <Width100>
      <Paragraph
        v-bind:center="true"
        title="User Interview"
        subtitle="Getting to know my users."
        paragraph="Before diving into the features and overall product, I conducted user interviews to understand the friction points in the tattoo experience and identify solutions to ease them. The interviews included people who are considering getting a tattoo, tattoo enthusiasts, and tattoo professionals."
      />
    </Width100>

    <Width100>
      <Width50>
        <OrderedList
          title="Observations"
          titleColor="#0074D9"
          v-bind:items="observations"
        />
      </Width50>
      <Width50>
        <OrderedList
          title="Key Insights"
          titleColor="#0074D9"
          v-bind:items="keyInsights"
        />
      </Width50>
    </Width100>
    <Width100>
      <VueImage :src="inktank_affinitymap1" alt="inktank affinity map" />
    </Width100>

    <LineBreak />

  </div>
</template>

<script>
import LineBreak from '../common/LineBreak';
import OrderedList from '../common/OrderedList';
import Paragraph from '../common/Paragraph';
import VueImage from '../common/VueImage';
import Width50 from '../common/layout/Width50';
import Width100 from '../common/layout/Width100';

import inktank_affinitymap1 from "../../assets/img/inktank_affinitymap1.svg";

export default {
  components: {
    LineBreak,
    OrderedList,
    Paragraph,
    Width50,
    Width100,
    VueImage,
  },
  data() {
    return {
      inktank_affinitymap1,
      observations: [
        "There's always consistent communication with the tattoo artist and clients. Tattoo artists always go back and forth with consultation and revision follow-up appointments.",
        "Most users prefer to search for things on Instagram and follow similar artists for awhile before making a decision.",
        "People often get tattoos for social reasons to give them a sense of belonging.",
      ],
      keyInsights: [
        "People like to follow their tattoo artist on social media and make their decisions after they keep liking their work for some time. People also like to see who their tattoo artists follow as well to find more similar designs.",
        "A direct messaging can help users to get to know their tattoo artist better before they even visit the shop.",
        "Sometimes people regret their tattoos because they didn't’t like how their artists designed it. There could be lack of communication or people don’t feel comfortable asking questions in person.",
        "Tattoo artist have to consistently stay updated with their appointments and it could get overwhelming. Having a way to track multiple appointments would be valuable.",
      ],
    };
  }

};
</script>



