<template>
  <ParagraphWithCarousel
    title="User Journies"
    subtitle="Shaping Ideas"
    paragraph="Understanding the decision making and flow within the tattoo experience allowed me to brainstorm the neccessary feature and oportunities required for Lauren and Danny to successully navigate through the app."
    :carouselStyle="containerStyle"
    :carouselItems="items"
    :imgStyle="imgStyle"
  />
</template>

<script>
import ParagraphWithCarousel from '../common/ParagraphWithCarousel';

import lauren_journeymap from "../../assets/img/lauren_journeymap.jpg";
import danny_journeymap from "../../assets/img/danny_journeymap.jpg";

export default {
  components: {
    ParagraphWithCarousel,
  },
  data() {
    return {
      items: [
        {img: lauren_journeymap, alt: "User Journey Map"},
        {img: danny_journeymap, alt: "User Journey Map"},
      ],
      containerStyle: {
        height: "100%"
      },
      imgStyle: {
        padding: "24px",
        width: "80%",
        margin: "0 auto",
      },
    };
  }

};
</script>

<style lang="scss" scoped>

</style>



