<template>
  <Width100>
    <VueImage :src="inktank_header_cover" alt="Landing banner" />
  </Width100>
</template>

<script>
import VueImage from '../common/VueImage';
import Width100 from '../common/layout/Width100';

import inktank_header_cover from "../../assets/img/inktank_header_cover.jpg";

export default {
  components: {
    VueImage,
    Width100,
  },
  data() {
    return {
      inktank_header_cover
    }
  }
};
</script>



