<template>
  <ParagraphWithCarousel
    title="Personas"
    paragraph="Based on the research found in the discovery period of the project, personas were developed. Designing for Lauren, Natalie, and Danny helped making informed design decisions for end user."
    :carouselStyle="containerStyle"
    :carouselItems="items"
    :imgStyle="imgStyle"
  />
</template>

<script>
import ParagraphWithCarousel from '../common/ParagraphWithCarousel';

import lauren_persona1 from "../../assets/img/lauren_persona1.jpg";
import natalie_persona2 from "../../assets/img/natalie_persona2.jpg";
import danny_persona3 from "../../assets/img/danny_persona3.jpg";

export default {
  components: {
    ParagraphWithCarousel,
  },
  data() {
    return {
      items: [
        {img: lauren_persona1, alt: "Persona"},
        {img: natalie_persona2, alt: "Persona"},
        {img: danny_persona3, alt: "Persona"},
      ],
      containerStyle: {
        height: "100%"
      },
      imgStyle: {
        padding: "24px",
        width: "80%",
        margin: "0 auto",
      },
    };
  }

};
</script>

<style lang="scss" scoped>

</style>



