<template>
  <div>
    <Width100>
      <Paragraph
        v-bind:center="true"
        title="Usability Testing"
        subtitle="Is it useful and functional?"
        paragraph="I tested the wireframes against key tasks with the Invision prototype. This helped me identify user frustrations I needed to address so I could improve my designs."
      />
    </Width100>

    <Width100 v-bind:addPadding="true">
      <Width50>
        <OrderedList
          title="Goals & Methodologies"
          titleColor="#0074D9"
          v-bind:items="goals"
        />
      </Width50>
      <Width50>
        <OrderedList
          title="Scenarios"
          titleColor="#0074D9"
          v-bind:items="scenarios"
        />
      </Width50>
    </Width100>
    <LineBreak />
  </div>
</template>

<script>
import LineBreak from '../common/LineBreak';
import OrderedList from '../common/OrderedList';
import Paragraph from '../common/Paragraph';
import Width50 from '../common/layout/Width50';
import Width100 from '../common/layout/Width100';

export default {
  components: {
    LineBreak,
    OrderedList,
    Paragraph,
    Width50,
    Width100,
  },
  data() {
    return {
      goals: [
        "The aim of the test is to take a closer look at the main functionalities of the app to determine its overall usefulness.",
        "Identify any problems in order to improve these elements prior further testing.",
        "I hope to understand my user's needs and gain new clarity regarding the navigation of the current app prototype.",
        "The usability tests will be conducted both moderated in-peron and moderated remote methods. Participants will use my iphone 8+ and Skype will be used to conduct the test in a moderated remote method.",
      ],
      scenarios: [
        "Imagine you are exploring through the app and you found a tattoo collection you like and would like to bookmark it.",
        "You want to create a new collection for your tattoo ideas.",
        "You are searching for tattoo pictures and found one you want to save.",
        "You found your tattoo artist and would like to schedule an appointment.",
      ],
    };
  }

};
</script>



