<template>
  <div>
    <Width100>
      <Paragraph
        v-bind:center="true"
        title="Key Learnings"
        paragraph="Sometimes a feature that seemed innovative on a design stand point, but the reality is that it might not translate well with the user understanding the feature. Designers have to learn to let go of the feature during the project."
      />
    </Width100>
    <Width100 v-bind:addPadding="true">
      <OrderedList
        title="Next Steps"
        titleColor="#0074D9"
        v-bind:items="nextSteps"
      />
    </Width100>
  </div>
</template>

<script>
import LineBreak from '../common/LineBreak';
import OrderedList from '../common/OrderedList';
import Paragraph from '../common/Paragraph';
import Width100 from '../common/layout/Width100';

export default {
  components: {
    LineBreak,
    OrderedList,
    Paragraph,
    Width100,
  },
  data() {
    return {
      nextSteps: [
        "I’d like to further test the home screen to see what other needs/wants users have.",
        "Include animation and see how users react to it.",
        "I’d like to further test the search screen to see if there are additional confusion over the search function.",
        "Design user onboarding screens to familiarize them with features within the app.",
      ],
    };
  }

};
</script>

<style lang="scss" scoped>
</style>



