<template>
  <ParagraphWithCarousel
    title="Prototyping & Testing"
    subtitle="Wireframes"
    paragraph="I created wireframes based on my userflow and made iterations from the feedback received during user testing."
    :carouselStyle="containerStyle"
    :carouselItems="items"
    :imgStyle="imgStyle"
  />
</template>

<script>
import ParagraphWithCarousel from '../common/ParagraphWithCarousel';

import midfidelity1 from "../../assets/img/midfidelity1.jpg";
import midfidelity2 from "../../assets/img/midfidelity2.jpg";
import midfidelity3 from "../../assets/img/midfidelity3.jpg";
import highfidelity1 from "../../assets/img/highfidelity1.jpg";
import highfidelity2 from "../../assets/img/highfidelity2.jpg";
import highfidelity3 from "../../assets/img/highfidelity3.jpg";

export default {
  components: {
    ParagraphWithCarousel,
  },
  data() {
    return {
      items: [
        {img: midfidelity1, alt: "Mid fidelity mocks"},
        {img: midfidelity2, alt: "Mid fidelity mocks"},
        {img: midfidelity3, alt: "Mid fidelity mocks"},
        {img: highfidelity1, alt: "High fidelity mocks"},
        {img: highfidelity2, alt: "High fidelity mocks"},
        {img: highfidelity3, alt: "High fidelity mocks"},
      ],
      containerStyle: {
        height: "100%"
      },
      imgStyle: {
        padding: "24px",
        width: "80%",
        margin: "0 auto",
      },
    };
  }

};
</script>

<style lang="scss" scoped>

</style>



